import React, { Component } from 'react';
import MarkdownPreview, {
  MarkdownAbstractSyntaxTree,
  NodeType,
} from '@uiw/react-markdown-preview';
import Code from './Code';

interface MarkdownProps {}
interface MarkdownState {
  mdStr: string;
}

/**
 * 
 *
 * ```md
 * <!--DemoStart,bgWhite,noCode,noPreview,noScroll,codePen-->
 * ```
 * 
 *
 * - `bgWhite` 
 * - `noCode` 
 * - `noPreview` 
 * - `noScroll` 
 * - `codePen`  Codepen  `` `import`  Codepen 
 */
export interface OptionsMarkdown {
  bgWhite?: boolean;
  noCode?: boolean;
  noPreview?: boolean;
  noScroll?: boolean;
  codePen?: boolean;
}

export default class Markdown extends Component<MarkdownProps, MarkdownState> {
  constructor(props: MarkdownProps) {
    super(props);
    this.state = {
      mdStr: '',
    };
  }
  getMdStr?: any;
  dependencies?: any;
  componentDidMount() {
    if (this.getMdStr) {
      this.getMdStr().then((str: any) => {
        this.setState({
          mdStr: str.default || str,
        });
      });
    }
  }
  render() {
    return (
      <MarkdownPreview
        style={{ padding: '20px 26px' }}
        source={this.state.mdStr}
        className="markdown"
        escapeHtml={false}
        allowNode={(
          node: MarkdownAbstractSyntaxTree,
          index: number,
          parent: NodeType,
        ) => {
          if (node.type === 'code') {
            const preIdx = index - 1;
            const nextIdx = index + 1;
            const childs: MarkdownAbstractSyntaxTree[] = (parent as any)
              .children;
            /**
             * 
             */
            if (
              childs &&
              childs.length > 0 &&
              childs[preIdx] &&
              childs[preIdx].type === 'html' &&
              /^<!--DemoStart\s?(.*)-->/.test(
                (childs[preIdx] && childs[preIdx].value) || '',
              ) &&
              childs[nextIdx] &&
              childs[nextIdx].type === 'html' &&
              childs[nextIdx] &&
              childs[nextIdx].value === '<!--End-->'
            ) {
              const preValue = (childs[preIdx] && childs[preIdx].value) || '';
              let options = {} as OptionsMarkdown;
              preValue.replace(
                /<!--\s?DemoStart\s?(.*)-->/g,
                (match: string, parame: any, code: any, offset: any) => {
                  options = parame
                    .replace(/^(,)/, '')
                    .split(',')
                    .map((val: string) => {
                      if (
                        val &&
                        val.includes('=') &&
                        val.split('=').length === 2
                      ) {
                        return {
                          [`${val.split('=')[0]}`]: val.split('=')[1],
                        };
                      }
                      return { [`${val}`]: true };
                    });
                  return '';
                },
              );
              node.value = `;{{/**${JSON.stringify(options)}**/}};${
                node.value
              }`;
            }
          }
          return true;
        }}
        renderers={{
          code: (props) => {
            return <Code {...props} dependencies={this.dependencies} />;
          },
        }}
      />
    );
  }
}
